body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.slider-background{
  width: 100%;
  height: 400px;
  position: fixed;
  top: 0px;
  z-index: -1;
  background: black;
}
.slider-container{
  overflow: hidden;
  width: 66%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
}
.border{
  height: 100%;
  position: absolute;
  top: 0px;
  width: 66%;
}
.cover-text {
  position: absolute;
  top: 50%;
  width: calc(100% + 10px);
  left: -5px;
  -webkit-transform: translate(-0%, -50%);
          transform: translate(-0%, -50%);
  text-align: center;
  z-index: 2; /* Ensures text stays on top */
  background-color: rgba(8, 66, 204, .6);
  background-color: rgba(0, 0, 0, 0.6); /* Dark semi-transparent background */
  border: 1px solid #bbb; /* Light border for a touch of refinement */
  -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px); /* Subtle blur for depth */
}

.cover-text h2, .cover-text h3 {
  margin: 0;
  padding: 10px 20px; /* Adequate padding for better structure */
  color: #f0f0f0; /* Light grey color for subtlety */
}

.cover-text h2 {
  font-size: 2rem; /* Appropriate size for main heading */
  font-weight: 600; /* Semi-bold for prominence */
}

.cover-text h3 {
  font-size: 1.25rem; /* Smaller than h2 */
  font-weight: normal; /* Normal weight to differentiate from h2 */
}

@media (max-width: 1024px) {
  .slider-container{
    width: 75%;
  }
  .border{
    width: 75%;
  }
}
@media (max-width: 700px) {
  .slider-container{
    width: 90%;
  }
  .border{
    width: 90%;
  }
}
.slider{
  height: 100%;
  width: 200%;
  display: inline-block;
}
.slider-animator{
  -webkit-animation-name: slide;
          animation-name: slide;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.slider-reset{
  height: 100%;
  width: 200%;
  display: inline-block;
  -webkit-animation-name: slide;
          animation-name: slide;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
@-webkit-keyframes slide{
  from {-webkit-transform: translateX(0);transform: translateX(0);}
  to {-webkit-transform: translateX(-50%);transform: translateX(-50%);}
}
@keyframes slide{
  from {-webkit-transform: translateX(0);transform: translateX(0);}
  to {-webkit-transform: translateX(-50%);transform: translateX(-50%);}
}
.slider .paralax-slide{
  width: 50%;
  background-position: center;
  display: inline-block;
  height: 100%;
}
/*
  PARALAX BACKGROUND
*/
.paralax-background{
  width: 100%;
  height: 550px;
  position: fixed;
  top: 0px;
  z-index: -1;
}

.background-content{
  height: 100%;
}
.tile-box{
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column;
  flex-wrap: wrap;
}

.tile{
  width: 33%;
  height: 269px;
  border: 1px;
  padding: 2px;
}
.tile img{
  width: auto;
  height: 100%;
  display: block;
}
@media (min-width: 1350px) {
  .tile{
    width: 25%;
  }
}
.abs-button{
  position: absolute;
}
/* flip card */
.flip-card {
  background-color: transparent;
  width: 100%;
  height: 100%;
  -webkit-perspective: 1000px;
          perspective: 1000px;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  -webkit-transition: -webkit-transform 0.8s;
  transition: -webkit-transform 0.8s;
  transition: transform 0.8s;
  transition: transform 0.8s, -webkit-transform 0.8s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.flipped {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.flip-card-front, .flip-card-back {
  position: absolute;
  top:0px;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  overflow: hidden;
}

.flip-card-back {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.homepage .content-container{
  margin-top: 400px;
  position: absolute;
  box-shadow: 0px -2px 10px 0px #000;
}
.homepage .content{
  position: relative;
  padding-top: 5px;
}

.prof-pic-container{
  width: 200px;
  height:200px;
  position: absolute;
  top:-100px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 1px 1px 9px;
  color:black !important;
}
#name{
  margin: 0px;
}
.profile{
  position: absolute;
  top:0px;
  width: 250px;
  padding-top: 100px;
  display: flex;
  align-items: center;
  flex-flow: column;
  height: calc(100% - 100px);
  background-color: #0842cc;
}
.profile > * {
  color: white;
}
.prof-pic{
  width: 100%;
  height: auto;
}
.sidebar{
  display: flex;
  height: 100%;
  align-items: flex-start;
  flex-flow: column;
  padding-left: 10px;
}
.sticky-sidebar{
  position: -webkit-sticky;
  position: sticky; 
  top: 0px;
}
.about h3 {
  color: #fff;
  margin: 5px 0 0 0;
}

.about p {
  padding: 0px;
  margin: 5px;
  font-size: 14px;
  color: #f0f0f0;
}

.sticky-sidebar ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.sticky-sidebar .sub-menu {
  border-left: 1px solid #062e7d;
  margin-left: 20px;
}

.anchor-link a {
  color: #fff;
  text-decoration: none;
  display: block;
  padding: 10px;
  border-bottom: 1px solid #062e7d;
}

.anchor-link a:hover {
  background-color: #062e7d;
}

.anchor-link:last-child a {
  border-bottom: none;
}

.homepage .content{
  background-color: white;
}
.main{
  margin-left: 250px;
  padding-left: 15px;
  padding-right: 15px;
}


/* Hambuger Menu */
.anchor-menu{
  position: -webkit-sticky;
  position: sticky;
  float: right;
  top:15px;
  right:15px;
  height: auto;
  z-index: 4;
}
.anchor-menu.open-menu{
  position: fixed;
}
.anchor-menu img{
  height: 35px;
  width: 35px;
  position: absolute;
  top: 5px;
  right: 5px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.dropdown-menu{
  z-index: 3;
  overflow: hidden;
  overflow-y: scroll; 
  position: fixed;
  right: 0px;
  top: 0px;
  width: 70vw;
  height:calc(100% - 50px);
  -webkit-transition: 0.8s;
  transition: 0.8s;
  background-color:rgba(8, 66, 204, .95);
  font-size: 27px;
  padding-top: 50px
}
.dropdown-menu ul {
  list-style: none;
  margin: 0;
  padding: 0;
  min-width: calc(70vw - 20px);
}

.dropdown-menu .sub-menu {
  margin-left: 20px;
  border-left: 1px solid #062e7d;
}
.hamburger-link a {
  display: block;
  padding: 15px;
  color: #fff; /* White text for visibility */
  text-decoration: none;
  border-bottom: 1px solid #062e7d; /* Darker blue border */
}

.hamburger-link a:hover {
  background-color: #062e7d; /* Darker blue on hover */
}
@media (min-width: 776px){
  .anchor-menu{
    visibility: hidden;
  }
}
@media (max-width: 776px){
  .main{
    margin-left:0px;
  }
  .profile{
    position: relative;
    width: 100%;
  }
  .about{
    text-align: center;
  }
  .sticky-sidebar{
    visibility: hidden;
    height: 0px;
  }

}
/* RESUME */

.resume {
  font-family: Arial, sans-serif;
  max-width: 800px;
  margin: 20px auto;
  background-color: #fff;
}

.resume h2, .projects h2 {
  font-size: 28px;
  border-bottom: 2px solid #0842cc;
  padding-bottom: 10px;
}

.resume .resume-header {
  font-size: 22px;
  color: #0842cc;
  margin-top: 20px;
  border-bottom: 1px solid #ccc;
  padding-bottom: 5px;
}

.resume ul {
  padding-left: 20px;
}

.resume li {
  margin-bottom: 5px;
  line-height: 1.5;
  position: relative; /* Position relative to place bullets correctly */
}


.resume a {
  color: #0842cc;
  text-decoration: none;
}

.resume a:hover {
  text-decoration: underline;
}

.job-title {
  font-weight: bold;
  color: #333;
  font-size: 19px;
  padding: 5px 0;
  margin-left: 10px;
  margin-bottom: 10px;
}

.job-year {
  font-style: italic;
  font-size: 14px;
  color: #555;
}

/*Projects*/
.projects .project-container {
  background-color: #f0f0f0;
  padding: 20px;
  margin: 10px 0;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.projects .project-header {
  color: #0842cc;
  margin: 0 0 10px 0;
}

.projects .project-desc {
  color: #333;
  font-size: 14px;
  line-height: 1.6;
}

.projects .carousel {
  margin-top: 20px;
}

/*Carousel*/

.carousel {
  position: relative;
  width: 500px;
  margin: 0 auto;
  height: auto;
  overflow: hidden;
  white-space: nowrap;
  background-color: #e0e0e0;
}

.slide-wrapper {
  position: relative;
  height: 100%;
  width: 100%;
}

.slide {
  display: inline-block;
  height: 100%;
  width: 100%;
}
.slide img{
  max-width: 100%;
  max-height: 550px;
  margin-left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
}
.desc-wrapper{
  white-space: normal;
  margin:5px 15px 5px 15px;
  background-color: white;
  padding:1px 10px 1px 10px;
  border-radius: 5px;
}
.current-slide{
  position: absolute;
  top:5px;
  right: 5px;
  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 7px;
  padding: 0 2px 0 2px;
}
.arrow{
  height: 100%;
  width: 50px;
  position: absolute;
  top: 0px;
  background-color: rgba(105, 105, 105, 0.1);
  opacity: .1;
  display: flex;
  align-items: center;
  -webkit-transition:.2s;
  transition:.2s;
}
.arrow img {
  width: 50px;
  height: 50px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  -webkit-transform: translateX(9px);
          transform: translateX(9px);
}

.prev-arrow{
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
  left:0px; 
}
.next-arrow{
  right: 0px;
}

.arrow:hover{
  opacity: .5;
}

.carousel-desc {
  background-color: rgba(0, 0, 0, 0.7);
  color: #fff;
  padding: 15px;
  border-radius: 0 0 8px 8px;
}

.desc-inner {
  font-size: 14px;
  line-height: 1.8;
  text-wrap: wrap;
}

